import axios from "axios";
import { API_BASE_URL } from '@/config.js';
import deliveryDecoration from "@/helpers/deliveryDecoration.js";

export default {
  data() {
    return ({
      deliveries: null,
    })
  },

  computed: {
    deliveriesInfo() {
      let result = "";
      if (this.deliveries) {
        this.deliveries.map(item => {
          result = Number(item.price) > 0 ? item.priceLabel : result;
        })
      } else {
        result = "Нет данных";
      }
      return result;
    },
  },

  methods: {
    loadDelivery() {
      this.startLoad();
      return axios.get(API_BASE_URL + "/api/deliveries")
        .then(response => {
          this.deliveries = deliveryDecoration(response);
          this.endLoadOk();
        })
        .catch((error) => {
          this.endLoadOk();
          return error;
        })
    },

  },
}
