<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">Каталог</h1>
        <span class="content__info"> {{ totalProduct }} товара </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-min.sync="filterMinPrice"
        :price-max.sync="filterMaxPrice"
        :category-id.sync="filterCategoryId"
        :color-ids.sync="filterColorIds"
        :material-ids.sync="filterMaterialIds"
        :season-ids.sync="filterSeasonIds"
      />

      <section class="catalog">
        <h3 v-show="(totalProduct === 0) & !showPreloader">Товары не найдены.</h3>

        <ul class="catalog__list" v-show="totalProduct > 0">
          <li class="catalog__item" v-for="(item, index) in products" :key="item.id">
            <ProductItem :product="item" :productIndex="index" />
          </li>
        </ul>

        <BasePagination
          :pagination="pagination"
          :listLimit="listLimit"
          @change-page="changePage"
        />
      </section>
    </div>

    <WarningMessage
      v-if="loadError"
      :errorMessage="errorMessage"
      @tryAgain="loadProducts"
    />

    <ShowPreloader v-show="showPreloader" />
  </main>
</template>

// 999999999999999 999999999999999999999999 99999999999999999 ------99999999999999999

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";
import showServiceMixin from "@/mixins/showServiceMixin";
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import forFilterMixin from "@/mixins/forFilterMixin.js";
import ProductItem from "@/components/ProductItem.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import BasePagination from "@/components/BasePagination.vue";
import { mapGetters, mapMutations } from "vuex";
import checkFillingGallery from "@/helpers/checkFillingGallery.js";

export default {
  components: {
    ProductItem,
    ProductFilter,
    BasePagination,
  },

  data() {
    return {
      products: null,

      pagination: {
        page: 1,
        limit: 0,
        totalPage: 0,
        totalProduct: 0,
      },

      listLimit: [9, 18, 27],
    };
  },

  mixins: [showServiceMixin, numberFilterMixin, forFilterMixin],

  computed: {
    ...mapGetters(["currentPageGetter", "currentLimitGetter"]),

    totalProduct() {
      return this.pagination.totalProduct;
    },
  },

  methods: {
    ...mapMutations(["updateCurrentCss", "updateCurrentLimit", "updateCurrentPage"]),

    changePage(valuePage, valueLimit) {
      this.pagination.page = valuePage;
      this.pagination.limit = valueLimit;
      this.loadProducts();
    },

    loadProducts() {
      clearTimeout(this.processLoadProduct);
      this.processLoadProduct = setTimeout(() => {
        this.startLoad();
        this.pagination.totalProduct = 0;
        this.pagination.totalPage = 0;

        return axios
          .get(API_BASE_URL + "/api/products", {
            params: {
              categoryId: this.filterCategoryId,
              materialIds: this.filterMaterialIds,
              seasonIds: this.filterSeasonIds,
              colorIds: this.filterColorIds,
              minPrice: this.filterMinPrice,
              maxPrice: this.filterMaxPrice,
              page: this.pagination.page,
              limit: this.pagination.limit,
            },
          })
          .then((respons) => {
            this.products = respons.data.items;
            this.products.forEach((product) => {
              product.colors.forEach((item) => {
                item.gallery = checkFillingGallery(item.gallery);
              });
            });
            if (respons.data.pagination) {
              this.pagination.totalProduct = respons.data.pagination.total;
              this.pagination.totalPage = respons.data.pagination.pages;
            }
          })
          .catch((error) => {
            this.endLoadError();
            this.fillErrorMessage(
              error,
              "Ошибка загрузки каталога товаров. Обновите страницу или попробуйте позже...",
              true
            );
          })
          .then(() => this.endLoadOk());
      }, 500);
    },
  },

  watch: {
    filterCategoryId() {
      this.loadProducts();
    },
    filterMaterialIds() {
      this.loadProducts();
    },
    filterSeasonIds() {
      this.loadProducts();
    },
    filterMinPrice() {
      this.loadProducts();
    },
    filterMaxPrice() {
      this.loadProducts();
    },
    filterColorIds() {
      this.loadProducts();
    },
  },

  created() {
    this.pagination.limit =
      this.pagination.limit === 0 ? this.listLimit[0] : this.pagination.limit;
    if (this.currentLimitGetter === 0) {
      this.updateCurrentLimit(this.listLimit[0]);
    } else {
      this.pagination.limit = this.currentLimitGetter;
    }
    if (this.currentPageGetter === 0) {
      this.updateCurrentPage(this.pagination.page);
    } else {
      this.pagination.page = this.currentPageGetter;
    }
    this.loadProducts();
  },
};
</script>
