<template>
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>

        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            value="0"
            v-model.number="filterMinPrice"
          />

          <span class="form__value">От</span>
        </label>

        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            value="12345"
            v-model.number="filterMaxPrice"
          />

          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>

        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="filterCategoryId"
          >
            <option value="0">Все категории</option>

            <option
              v-for="itemCategory in categories"
              :key="itemCategory.id"
              :value="itemCategory.id"
            >
              {{ itemCategory.title }}
            </option>
          </select>

          <span v-if="categories.length === 0">
            Список категорий товаров не удалось загрузить
          </span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>

        <ul class="check-list colors colors--black">
          <li class="colors__item" v-for="itemColor in colors" :key="itemColor.code">
            <label class="check-list__label colors__label">
              <input
                class="colors__radio sr-only"
                type="checkbox"
                name="color"
                :id="itemColor.id"
                :value="itemColor.id"
                v-model="filterColorIds"
              />

              <span class="colors__value" :style="{ background: itemColor.code }"></span>
            </label>
          </li>
          <span class="colors__value" v-if="colors.length === 0">
            Список цветов не удалось загрузить
          </span>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>

        <span class="check-list__item" v-if="materials.length === 0">
          Список материалов не удалось загрузить
        </span>

        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="itemMaterial in materials"
            :key="itemMaterial.id"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="materials"
                :id="itemMaterial.code"
                :value="itemMaterial.id"
                v-model="filterMaterialIds"
              />

              <span class="check-list__desc">
                {{ itemMaterial.title }}
                <span> ({{ itemMaterial.productsCount }}) </span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>

        <span class="check-list__item" v-if="seasons.length === 0">
          Список коллекций не удалось загрузить
        </span>

        <ul class="check-list">
          <li class="check-list__item" v-for="itemSeason in seasons" :key="itemSeason.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collections"
                :id="itemSeason.code"
                :value="itemSeason.id"
                v-model="filterSeasonIds"
              />

              <span class="check-list__desc">
                {{ itemSeason.title }}
                <span> ({{ itemSeason.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button
        class="filter__submit button button--primery"
        type="submit"
        @click="clickSubmit"
      >
        Применить
      </button>

      <button
        class="filter__reset button button--second"
        type="button"
        @click.prevent="reset"
        v-show="selectedFilter"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

// 999999999999999999 999999999999999999

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";
import { mapMutations } from "vuex";
import forFilterMixin from "@/mixins/forFilterMixin.js";

export default {
  props: ["priceMin", "priceMax", "categoryId", "colorIds", "materialIds", "seasonIds"],

  data() {
    return {
      materials: [],
      categories: [],
      seasons: [],
      colors: [],
    };
  },

  mixins: [forFilterMixin],

  computed: {
    selectedFilter() {
      return (
        this.filterCategoryId > 0 ||
        this.filterMaterialIds.length > 0 ||
        this.filterSeasonIds.length > 0 ||
        this.filterColorIds.length > 0 ||
        this.filterMinPrice > 0 ||
        this.filterMaxPrice > 0
      );
    },
  },

  methods: {
    ...mapMutations([
      "updateIsFiltered",
      "updateFilterCategoryId",
      "updateFilterMaterialIds",
      "updateFilterSeasonIds",
      "updateFilterMinPrice",
      "updateFilterMaxPrice",
      "updateFilterColorIds",
    ]),

    clickSubmit() {
      this.updateIsFiltered(true);
    },

    updateFilter(categoryId, materialIds, seasonIds, minPrice, maxPrice, colorIds) {
      this.updateFilterCategoryId(categoryId);
      this.updateFilterMaterialIds(materialIds);
      this.updateFilterSeasonIds(seasonIds);
      this.updateFilterMinPrice(minPrice);
      this.updateFilterMaxPrice(maxPrice);
      this.updateFilterColorIds(colorIds);
    },

    restoreFilter() {
      this.filterCategoryId = this.$store.state.filterCategoryId;
      this.filterSeasonIds = this.$store.state.filterSeasonIds;
      this.filterMaterialIds = this.$store.state.filterMaterialIds;
      this.filterColorIds = this.$store.state.filterColorIds;
      this.filterMinPrice = this.$store.state.filterMinPrice;
      this.filterMaxPrice = this.$store.state.filterMaxPrice;
    },

    submit() {
      this.$emit("update:priceMin", this.filterMinPrice);
      this.$emit("update:priceMax", this.filterMaxPrice);
      this.$emit("update:categoryId", this.filterCategoryId);
      this.$emit("update:colorIds", this.filterColorIds);
      this.$emit("update:materialIds", this.filterMaterialIds);
      this.$emit("update:seasonIds", this.filterSeasonIds);
      this.updateFilter(
        this.filterCategoryId,
        this.filterMaterialIds,
        this.filterSeasonIds,
        this.filterMinPrice,
        this.filterMaxPrice,
        this.filterColorIds
      );
    },

    reset() {
      this.filterCategoryId = 0;
      this.filterSeasonIds = [];
      this.filterMaterialIds = [];
      this.filterColorIds = [];
      this.filterMinPrice = 0;
      this.filterMaxPrice = 0;
      this.updateIsFiltered(false);
      this.submit();
    },

    loadMaterials() {
      this.materials = [];
      return axios(API_BASE_URL + "/api/materials")
        .then((respons) => {
          this.materials = respons.data.items ? respons.data.items : [];
        })
        .catch(() => (this.materials = []));
    },

    loadCategories() {
      this.categories = [];
      return axios(API_BASE_URL + "/api/productCategories")
        .then((respons) => {
          this.categories = respons.data.items ? respons.data.items : [];
        })
        .catch(() => (this.categories = []));
    },

    loadSeasons() {
      this.seasons = [];
      return axios(API_BASE_URL + "/api/seasons")
        .then((respons) => {
          this.seasons = respons.data.items ? respons.data.items : [];
        })
        .catch(() => (this.seasons = []));
    },
    loadColors() {
      this.colors = [];
      return axios(API_BASE_URL + "/api/colors")
        .then((respons) => {
          this.colors = respons.data.items ? respons.data.items : [];
        })
        .catch(() => (this.colors = []));
    },
  },

  created() {
    if (this.categories.length === 0) {
      this.loadCategories();
    }
    if (this.materials.length === 0) {
      this.loadMaterials();
    }
    if (this.seasons.length === 0) {
      this.loadSeasons();
    }
    if (this.colors.length === 0) {
      this.loadColors();
    }
    if (this.$store.state.isFiltered) {
      this.restoreFilter();
      this.submit();
    }
  },
};
</script>
