// в доставку добавить поле с преобраз ценой( 1 200 Р или бесплатно)
import deliveryPriceLabel from "@/helpers/deliveryPriceLabel.js";

export default function deliveryDecoration(array) {
  const result = array.data.map(item => {
    return (
      {
        ...item,
        priceLabel: deliveryPriceLabel(item.price),
      }
    )
  });
  return result;
}
