<template>
  <div>
    <HeaderApp />

    <router-view />

    <WarningMessage v-if="loadError" :errorMessage="errorMessage" @tryAgain="loadCart" />

    <ShowPreloader v-show="showPreloader" />

    <FooterApp />
  </div>
</template>

<script>
import HeaderApp from "@/components/HeaderApp.vue";
import FooterApp from "@/components/FooterApp.vue";
import showServiceMixin from "@/mixins/showServiceMixin";
import { mapActions, mapMutations } from "vuex";

export default {
  components: { HeaderApp, FooterApp },

  data() {
    return {};
  },

  mixins: [showServiceMixin],

  created() {
    this.startLoad();
    const userKey = localStorage.getItem("userAccessKey");

    if (userKey) {
      this.$store.commit("updateUserAccessKey", userKey);
    }
    this.loadCart()
      .then(() => {
        this.endLoadOk();
      })
      .catch((error) => {
        this.endLoadError();
        this.fillErrorMessage(error, "Ошибка загрузки данных. Попробуйте позже...", true);
      });
  },

  methods: {
    ...mapActions(["loadCart"]),
    ...mapMutations(["updateUserAccessKey"]),
  },
};
</script>
