<template>
  <label
    class="form__label form__label--small form__label--select"
    :class="smaller ? 'form__label--smaller' : ''"
  >
    <select
      class="form__select"
      :class="smaller ? 'form__select--smaller' : ''"
      type="text"
      name="size"
      v-if="sizes"
      v-model.number="selectSizeId"
      @change.prevent="changeSize"
    >
      <option v-for="itemSize in sizes" :key="itemSize.id" :value="itemSize.id">
        {{ itemSize.title }}
      </option>
    </select>
  </label>
</template>

<script>
export default {
  props: ["sizes", "selectSize", "smaller"],

  data() {
    return {
      selectSizeId: 0,
    };
  },

  watch: {
    sizes() {
      if (this.sizes.length > 0) {
        this.selectSizeId = this.sizes[0].id;
      }
    },
  },

  methods: {
    changeSize() {
      this.$emit("update:selectSize", this.selectSizeId);
    },
  },

  created() {
    if (this.sizes.length > 0) {
      this.selectSizeId = this.sizes[0].id;
    }
  },
};
</script>

<style>
.form__label--smaller {
  margin-bottom: 10px !important;
}

.form__select--smaller {
  height: 27px !important;
  font-size: 14px;
}
</style>
