<template>
  <div>
    <router-link class="catalog__pic" :to="{ name: 'card', params: { id: product.id } }">
      <img :src="urlImage" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <router-link :to="{ name: 'card', params: { id: product.id } }">
        {{ product.title }}
      </router-link>
    </h3>

    <div style="display: flex; align-items: center; justify-content: space-between">
      <span class="catalog__price"> {{ product.price | numberFormat }} ₽ </span>

      <SelectSize :sizes="sizes" :select-size.sync="selectSizeId" :smaller="true" />

      <button
        class="button--primery tooltip"
        data-title="Укажите цвет, размер и нажмите эту кнопку для добавления в корзину"
        @click.prevent="productAddCart"
        style="border-radius: 2px; margin-bottom: 10px"
        v-if="sizes.length > 0"
      >
        <svg width="29" height="20">
          <use xlink:href="#icon-cart"></use>
        </svg>
      </button>
    </div>

    <ul class="colors colors--black">
      <li
        class="colors__item"
        v-for="(itemColor, i) in product.colors"
        :key="itemColor.color.id"
      >
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            :name="product.id"
            type="radio"
            :value="i"
            v-model="indexColor"
          />

          <span class="colors__value" :style="{ background: itemColor.color.code }">
          </span>
        </label>
      </li>
    </ul>
    <WarningMessage
      v-if="loadError"
      :errorMessage="errorMessage"
      @tryAgain="loadProduct"
    />

    <ShowPreloader v-show="showPreloader" />
  </div>
</template>

// 88888888 888888888888888888888888888

<script>
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import SelectSize from "@/components/SelectSize.vue";
import { mapActions } from "vuex";
import showServiceMixin from "@/mixins/showServiceMixin";
import { NO_FOTO } from "@/config.js";

export default {
  props: ["product", "productIndex"],

  components: { SelectSize },

  data() {
    return {
      urlImage: "",
      indexColor: 0,
      selectSizeId: 0,
      sizes: [],
    };
  },

  mixins: [numberFilterMixin, showServiceMixin],

  computed: {
    selectColorId() {
      return this.product.colors[this.indexColor].color.id;
    },
  },

  methods: {
    ...mapActions(["loadProductById", "loadCart", "addToCart"]),

    productAddCart() {
      if (this.product) {
        this.startLoad();
        this.addToCart({
          productId: this.product.id,
          colorId: this.selectColorId,
          sizeId: this.selectSizeId,
          quantity: 1,
        })
          .then(() => {
            this.endLoadOk();
          })
          .catch((error) => {
            this.endLoadError();
            this.fillErrorMessage(
              error,
              "Ошибка добавления товара в корзину. Обновите страницу или попробуйте позже...",
              true
            );
          })
          .then(() => {
            this.startLoad();
            this.loadCart()
              .then(() => {
                this.endLoadOk();
              })
              .catch((error) => {
                this.endLoadError();
                this.fillErrorMessage(
                  error,
                  "Ошибка загрузки корзины с товарами. Обновите страницу или попробуйте позже...",
                  true
                );
              });
          });
      }
    },

    changeImage() {
      if (this.product.colors[this.indexColor].gallery) {
        this.urlImage = this.product.colors[this.indexColor].gallery[0].file.url;
      } else {
        this.urlImage = NO_FOTO;
      }
    },

    loadSizes() {
      this.loadProductById(this.product.id)
        .then((respons) => {
          this.sizes = respons.data.sizes;
          if (this.sizes.length > 0) {
            this.selectSizeId = this.sizes[0].id;
          }
        })
        .catch(() => {
          this.sizes = [];
        });
    },
  },

  watch: {
    indexColor() {
      this.changeImage();
    },

    sizes() {
      if (this.sizes.length > 0) {
        this.selectSizeId = this.sizes[0].id;
      }
    },
  },

  created() {
    this.loadSizes();
    this.changeImage();
  },
};
</script>
