import BaseFormFeild from "@/components/BaseFormFeild.vue";

export default {
  props: ["title", "placeholder", "value", "error", "name"],

  components: { BaseFormFeild },

  computed: {
    dataValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
        this.saveValue(value);
      }
    },
  },

  methods: {
    restoreValue() {
      let value = localStorage.getItem(`${this.name}`);
      if (!value) {
        value = ""
      }
      this.$emit("input", value);
    },

    saveValue(newValue) {
      if (this.name !== "comment") {
        if (newValue) {
          localStorage.setItem(`${this.name}`, newValue);
        }
      }
    },
  },

  created() {
    this.restoreValue();
  }

}
