import axios from "axios";
import Vue from "vue";
import Vuex from "vuex";
import { API_BASE_URL } from "@/config.js";
import forFilterMixin from "@/mixins/forFilterMixin.js";
import checkFillingGallery from '@/helpers/checkFillingGallery';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userAccessKey: null,

    isFiltered: false,         //отфильтровано
    productAddedCart: false,   //в корзину добавлено

    cartProducts: [],           //содержимое корзины

    currentPage: 0,      //текущ стр.
    currentLimit: 0,    //текущ лимит

  },

  mixins: [forFilterMixin],

  mutations: {
    updateCurrentPage(state, value) {
      state.currentPage = value;
    },

    updateCurrentLimit(state, value) {
      state.currentLimit = value;
    },

    updateProductAddedCart(state, value) {
      state.productAddedCart = value;
    },

    updateUserAccessKey(state, key) {
      state.userAccessKey = key;
    },

    updateCartProducts(state, value) {
      if (value) {
        value.forEach((item) => {
          item.color.gallery = checkFillingGallery(item.color.gallery)
        })
      }
      state.cartProducts = value;
    },

    updateFilterCategoryId(state, categoryId) {
      state.filterCategoryId = categoryId;
    },
    updateFilterMaterialIds(state, materialIds) {
      state.filterMaterialIds = materialIds;
    },
    updateFilterSeasonIds(state, seasonIds) {
      state.filterSeasonIds = seasonIds;
    },
    updateFilterMinPrice(state, minPrice) {
      state.filterMinPrice = minPrice;
    },
    updateFilterMaxPrice(state, maxPrice) {
      state.filterMaxPrice = maxPrice;
    },
    updateFilterColorIds(state, colorIds) {
      state.filterColorIds = colorIds;
    },
    updateIsFiltered(state, value) {
      state.isFiltered = value;
    },

  },//mutations:

  getters: {
    currentPageGetter(state) {
      return state.currentPage;
    },

    currentLimitGetter(state) {
      return state.currentLimit;
    },

    cartProductsGetter(state) {
      return state.cartProducts;
    },

    cartCauntProductGetter(state, getters) {
      return getters.cartProductsGetter.length;
    },

    productAddedCartGetter(state) {
      return state.productAddedCart
    },

    cartFinalPriceGetter(state, getters) {
      let result = 0;
      if (getters.cartProductsGetter.length > 0) {
        result = getters.cartProductsGetter.reduce(
          (allPrice, item) => item.price * item.quantity + allPrice, 0
        );
      }
      return result;
    },

    userAccessKeyGetter(state) {
      return state.userAccessKey;
    }
  },//getters:

  actions: {
    loadProductById(context, productId) {
      return (axios.get(API_BASE_URL + "/api/products/" + productId)
        .then(respons => {
          return respons
        })
        .catch(error => {
          return error
        })
      )
    },

    deleteCartProduct(context, { itemId }) {
      return (axios.delete(API_BASE_URL + "/api/baskets/products",
        {
          data: { basketItemId: itemId },
          params: { userAccessKey: context.state.userAccessKey }
        })
        .then((respons) => {
          context.commit("updateCartProducts", respons.data.items)
        })
        .catch((error) => {
          return error
        })
      )
    },

    changeQuantityProduct(context, { itemId, quantity }) {
      return (axios.put(API_BASE_URL + "/api/baskets/products",
        {
          basketItemId: itemId, quantity: quantity
        },
        {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then((respons) => {
          context.commit("updateCartProducts", respons.data.items)
        })
        .catch((error) => {
          return error;
        })
      )
    },

    addToCart(context, { productId, colorId, sizeId, quantity }) {
      return (
        axios.post(API_BASE_URL + "/api/baskets/products", {
          productId: productId, colorId: colorId, sizeId: sizeId, quantity: quantity
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
          .then((respons) => {
            context.state.productAddedCart = true;
            context.commit("updateCartProducts", respons.data.items)
            if (!context.state.userAccessKey) {
              context.commit("updateUserAccessKey", respons.data.user.accessKey);
            }

          })
          .catch((error) => {
            return error
          })
      )
    },

    loadCart(context) {
      return (axios
        .get(API_BASE_URL + "/api/baskets", {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(respons => {
          context.commit("updateCartProducts", respons.data.items)
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', respons.data.user.accessKey);
            context.commit("updateUserAccessKey", respons.data.user.accessKey);
          }
        })
      )
    },
  },//actions:

}) //export default
