<template>
  <div>
    <ul class="cart__list">
      <li class="cart__item product">
        <div class="product__pic">
          <img
            :src="cartItem.color.gallery[0].file.url"
            width="120"
            height="120"
            :alt="cartItem.product.title"
          />
        </div>

        <h3 class="product__title">{{ cartItem.product.title }}</h3>

        <p class="product__info product__info--color">
          Цвет:

          <span>
            <i :style="{ background: cartItem.color.color.code }"></i>
            {{ cartItem.color.color.title }}
          </span>

          <span> Размер: {{ cartItem.size.title }} </span>
        </p>

        <span class="product__code"> Артикул: {{ cartItem.product.id }} </span>

        <div class="product__counter form__counter">
          <QuantityMinus
            :width="10"
            :height="10"
            @quantity-minus="changeQuantity"
          />

          <input type="text" v-model.number="quantity" name="count" />

          <QuantityPlus
            :width="10"
            :height="10"
            @quantity-plus="changeQuantity"
          />
        </div>

        <b class="product__price"> {{ cartItem.price | numberFormat }} ₽ </b>

        <button
          class="product__del button-del"
          type="button"
          aria-label="Удалить товар из корзины"
          @click="deleteProduct"
        >
          <svg width="20" height="20" fill="currentColor">
            <use xlink:href="#icon-close"></use>
          </svg>
        </button>
      </li>
    </ul>
    <WarningMessage
      v-if="loadError"
      :errorMessage="errorMessage"
      @tryAgain="loadProduct"
    />

    <ShowPreloader v-show="showPreloader" />
  </div>
</template>

<script>
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import QuantityMinus from "@/components/QuantityMinus.vue";
import QuantityPlus from "@/components/QuantityPlus.vue";
import { mapActions } from "vuex";
import showServiceMixin from "@/mixins/showServiceMixin";

export default {
  props: ["cartItem"],

  components: { QuantityMinus, QuantityPlus },

  mixins: [numberFilterMixin, showServiceMixin],

  computed: {
    quantity: {
      get() {
        return this.cartItem.quantity;
      },
      set(value) {
        clearTimeout(this.timeOut);
        this.timeOut = setTimeout(() => {
          this.startLoad();
          if (value < 1 || value === "") {
            value = 1;
          }
          this.changeQuantityProduct({
            itemId: this.cartItem.id,
            quantity: value,
          })
            .then(() => {
              this.endLoadOk();
            })
            .catch((error) => {
              this.endLoadError();
              this.fillErrorMessage(
                error,
                "Ошибка изменения количества товара.",
                false
              );
            });
        }, 700);
      },
    },
  },

  methods: {
    ...mapActions(["changeQuantityProduct", "deleteCartProduct"]),

    deleteProduct() {
      this.startLoad();
      this.deleteCartProduct({ itemId: this.cartItem.id })
        .then(() => {
          this.endLoadOk();
        })
        .catch((error) => {
          this.endLoadError();
          this.fillErrorMessage(error, "Ошибка при удалении товара.", false);
        });
    },

    changeQuantity(value) {
      this.quantity += value;
    },
  },
}; //export
</script>
