<template>
  <div>
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог </router-link
            >>
          </li>

          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
              Корзина </router-link
            >>
          </li>

          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> Оформление заказа </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен <span>№ {{ orderInfo.id }}</span>
        </h1>
      </div>

      <section class="cart" v-if="orderInfo">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо
              с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение
              часа для уточнения деталей доставки.
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key"> Получатель </span>
                <span class="dictionary__value">
                  {{ orderInfo.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Адрес доставки </span>
                <span class="dictionary__value">
                  {{ orderInfo.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Телефон </span>
                <span class="dictionary__value">
                  {{ orderInfo.phone | VMask("# ### ### ## ## ## ##") }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Email </span>
                <span class="dictionary__value">
                  {{ orderInfo.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Способ оплаты </span>
                <span class="dictionary__value">
                  {{ orderInfo.paymentType }}
                </span>
              </li>
            </ul>
          </div>

          <OrderContent
            :current-price-label="priceLabel"
            :show-button="false"
            :cart-products="orderItems"
            :cart-final-price="orderInfo.totalPrice"
            v-if="orderInfo"
          />
        </form>
      </section>
      <ShowPreloader v-show="showPreloader" />

      <WarningMessage v-if="loadError" :errorMessage="errorMessage" />
    </main>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";
import deliveryPriceLabel from "@/helpers/deliveryPriceLabel.js";
import OrderContent from "@/components/OrderContent.vue";
import showServiceMixin from "@/mixins/showServiceMixin";

export default {
  components: { OrderContent },

  data() {
    return {
      orderInfo: {},
      priceLabel: "",
      orderItems: [],
    };
  },

  mixins: [showServiceMixin],

  computed: {
    ...mapGetters({
      userAccessKey: "userAccessKeyGetter",
    }),

    deliveryPrice() {
      return this.orderInfo.deliveryType.price
        ? Number(this.orderInfo.deliveryType.price)
        : 0;
    },
  },

  methods: {
    loadOrder() {
      this.startLoad();
      return axios
        .get(API_BASE_URL + "/api/orders/" + this.$route.params.id, {
          params: {
            userAccessKey: this.userAccessKey,
          },
        })
        .then((response) => {
          this.orderInfo = response.data;
          this.priceLabel = deliveryPriceLabel(this.orderInfo.deliveryType.price);
          this.orderItems = this.orderInfo.basket.items;
          this.endLoadOk();
        })
        .catch((error) => {
          this.endLoadError();
          this.orderInfo = {};
          this.fillErrorMessage(
            error,
            "Ошибка загрузки данных о подтвержденнном заказе. Попробуйте позже...",
            false
          );
        });
    },
  },

  created() {
    this.loadOrder();
  },
};
</script>
