<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>

        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">Корзина</h1>
        <span class="content__info"> {{ cartProducts.length }} товара </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <CartItem v-for="item in cartProducts" :key="item.id" :cartItem="item" />
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ this.cartFinalPrice | numberFormat }} ₽</span>
          </p>

          <button
            class="cart__button button button--primery"
            type="submit"
            v-show="this.cartFinalPrice > 0"
            @click.prevent="clickSubmit"
          >
            Оформить заказ
          </button>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from "vuex";
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import CartItem from "@/components/CartItem.vue";

export default {
  components: {
    CartItem,
  },

  mixins: [numberFilterMixin],

  computed: {
    ...mapGetters({
      cartProducts: "cartProductsGetter",
      cartFinalPrice: "cartFinalPriceGetter",
    }),
  },

  methods: {
    clickSubmit() {
      this.$router.push({ name: "order" });
    },
  },
};
</script>
