import showWarningMixin from "@/mixins/showWarningMixin.js";
import showPreloadMixin from '@/mixins/showPreloadMixin.js'

export default {
  mixins: [showWarningMixin, showPreloadMixin],

  methods: {
    startLoad() {
      this.loadError = false;
      this.showPreloader = true;
    },
    endLoadOk() {
      this.showPreloader = false;
      this.loadError = false;
    },
    endLoadError() {
      this.loadError = true;
      this.showPreloader = false;
    },
  }
}
