import WarningMessage from "@/components/WarningMessage.vue"

export default {
  components: { WarningMessage },

  data() {
    return {
      //ошибки справочников
      loadError: false,
      // productError: false,
      // cartError: false,
      // orderError: false,
      errorMessage: {
        responsText: '',   //ответ от сервера из error
        textForUser: '',   //текст сообщения
        showButton: false,
      }
    }
  },

  methods: {
    fillErrorMessage(error, textForUser, showButton) {
      this.errorMessage.responsText = error.code + ". " + error.message; //ответ от сервера из error
      this.errorMessage.textForUser = textForUser;       //текст сообщения
      this.errorMessage.showButton = showButton;
    }
  }
}
