<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>

        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>

        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">Оформление заказа</h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              title="ФИО"
              placeholder="Введите ваше полное имя"
              :error="formError.name"
              name="name"
            />

            <BaseFormText
              v-model="formData.address"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
              :error="formError.address"
              name="address"
            />

            <BaseFormText
              v-model="formData.phone"
              title="Телефон"
              placeholder="Введите ваш телефон"
              :error="formError.phone"
              type="tel"
              name="phone"
              :vmask="'###########'"
              data-title="+ и 11 цифр без пробелов и тире"
            />

            <BaseFormText
              v-model="formData.email"
              title="Email"
              placeholder="Введите ваш Email"
              :error="formError.email"
              type="email"
              name="email"
            />

            <BaseFormTextarea
              v-model="formData.comments"
              title="Комментарий к заказу"
              placeholder="Ваши пожелания"
              :error="formError.comment"
              name="comment"
              data-title="Мы внимательно отнесемся к вашим пожеланиям"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>

            <h4 v-if="!deliveries">Нет данных о способах доставки</h4>

            <ul class="cart__options options">
              <li
                class="options__item"
                v-for="itemDelivery in deliveries"
                :key="itemDelivery.id"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    :value="itemDelivery.id"
                    v-model.number="currentDelivery"
                  />

                  <span class="options__value">
                    {{ itemDelivery.title }} <b>{{ itemDelivery.priceLabel }}</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>

            <h4 v-if="!payments">Нет данных о способах оплаты</h4>

            <ul class="cart__options options">
              <li
                class="options__item"
                v-for="(itemPayment, index) in payments"
                :key="index"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    :value="itemPayment.id"
                    v-model.number="currentPayment"
                  />

                  <span class="options__value">
                    {{ itemPayment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <OrderContent
          :current-price-label="currentPriceLabel"
          :show-button="(currentPayment > 0) & (currentDelivery > 0)"
          :cart-products="cartProducts"
          :cart-final-price="cartFinalPrice + currentPrice"
          @click-submit="clickSubmit"
        />

        <div class="cart__error form__error-block" v-if="isFormError || loadError">
          <h4>Заявка не отправлена!</h4>

          <p>
            Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите
            страницу.
          </p>
        </div>
      </form>
    </section>
    <ShowPreloader v-show="showPreloader" />
    <WarningMessage
      v-if="loadError"
      :errorMessage="errorMessage"
      @tryAgain="clickSubmit"
    />
  </main>
</template>

////// 888888888 88888------------------------------------------- 8888888

<script>
import { API_BASE_URL } from "@/config";
import showServiceMixin from "@/mixins/showServiceMixin";
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import { mapGetters, mapMutations } from "vuex";
import deliveryMixin from "@/mixins/deliveryMixin.js";
import axios from "axios";
import OrderContent from "@/components/OrderContent.vue";
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";

export default {
  components: { OrderContent, BaseFormText, BaseFormTextarea },

  data() {
    return {
      payments: null,
      currentPayment: 0,
      currentDelivery: 0,
      formData: {},
      formError: {},
      isFormError: false,
    };
  },

  mixins: [showServiceMixin, numberFilterMixin, deliveryMixin],

  computed: {
    ...mapGetters({
      userAccessKey: "userAccessKeyGetter",
      cartProducts: "cartProductsGetter",
      cartFinalPrice: "cartFinalPriceGetter",
    }),

    currentDeliveryIndex() {
      return this.currentDelivery
        ? this.deliveries.findIndex((item) => item.id === this.currentDelivery)
        : -1;
    },

    currentPriceLabel() {
      return this.currentDeliveryIndex >= 0
        ? this.deliveries[this.currentDeliveryIndex].priceLabel
        : "";
    },

    currentPrice() {
      return this.currentDeliveryIndex >= 0
        ? Number(this.deliveries[this.currentDeliveryIndex].price)
        : 0;
    },
  },

  watch: {
    currentDelivery() {
      this.loadPayments();
    },
  },

  methods: {
    ...mapMutations(["updateCartProducts"]),

    clickSubmit() {
      this.startLoad();
      return axios
        .post(
          API_BASE_URL + "/api/orders",
          {
            ...this.formData,
            deliveryTypeId: this.currentDelivery,
            paymentTypeId: this.currentPayment,
          },
          {
            params: {
              userAccessKey: this.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.endLoadOk();
          this.updateCartProducts([]);
          this.$router.push({ name: "confirm", params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = {};
          if (error.response.data.error.request) {
            this.showPreloader = false;
            this.formError = error.response.data.error.request;
          } else {
            this.endLoadError();
            this.fillErrorMessage(
              error,
              "Ошибка отправки заказа. Обновите страницу или попробуйте позже...",
              true
            );
          }
          this.isFormError = Object.keys(this.formError).length > 0;
        });
    },

    loadPayments() {
      this.startLoad();
      if (this.currentDelivery > 0) {
        return axios
          .get(API_BASE_URL + "/api/payments", {
            params: {
              deliveryTypeId: this.currentDelivery,
            },
          })
          .then((respons) => {
            this.payments = respons.data;
            this.endLoadOk();
            if (this.payments) {
              this.currentPayment = this.payments[0].id;
            }
          })
          .catch(() => {
            this.payments = null;
            this.endLoadOk();
          });
      } else {
        this.payments = null;
      }
    },
  },

  created() {
    this.startLoad();
    this.loadDelivery()
      .then(() => {
        if (this.deliveries) {
          this.endLoadOk();
          this.currentDelivery = this.deliveries[0].id;
          this.loadPayments();
        }
      })
      .catch(() => {
        this.deliveries = null;
        this.endLoadOk();
      });
  },
}; //export
</script>
