<template>
  <div>
    <main class="content container">
      <h3 v-if="!product">Нет данных о продукте</h3>
      <div v-if="product">
        <div class="content__top">
          <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
                Каталог
              </router-link>
            </li>

            <li class="breadcrumbs__item">
              <a class="breadcrumbs__link" href="#" @click.prevent="updateCategoryId">
                {{ product.category.title }}
              </a>
            </li>

            <li class="breadcrumbs__item">
              <a class="breadcrumbs__link">
                {{ product.title }}
              </a>
            </li>
          </ul>
        </div>

        <section class="item">
          <div class="item__pics pics">
            <div class="pics__wrapper">
              <img width="570" height="570" :src="urlImage" :alt="product.title" />
            </div>

            <ul class="pics__list">
              <li class="pics__item" v-for="(item, i) in urlImages" :key="i">
                <a
                  class="pics__link pics__link--current"
                  @click.prevent="сhangeUrlImage(i)"
                >
                  <img width="98" height="98" :src="item" :alt="product.title" />
                </a>
              </li>
            </ul>
          </div>

          <div class="item__info">
            <span class="item__code">Артикул: {{ product.id }}</span>
            <h2 class="item__title">
              {{ product.title }}
            </h2>

            <div class="item__form">
              <form class="form" action="#" method="POST" @submit.prevent="submit">
                <div class="item__row item__row--center">
                  <div class="form__counter">
                    <QuantityMinus
                      :width="12"
                      :height="12"
                      @quantity-minus="changeQuantity"
                    />

                    <input type="text" value="1" name="count" v-model.number="quantity" />

                    <QuantityPlus
                      :width="12"
                      :height="12"
                      @quantity-plus="changeQuantity"
                    />
                  </div>

                  <b class="item__price"> {{ product.price | numberFormat }} ₽ </b>
                </div>

                <div class="item__row">
                  <fieldset class="form__block">
                    <legend class="form__legend">Цвет</legend>

                    <ul class="colors colors--black" v-if="product.colors">
                      <li
                        class="colors__item"
                        v-for="itemColor in product.colors"
                        :key="itemColor.color.id"
                      >
                        <label class="colors__label">
                          <input
                            class="colors__radio sr-only"
                            type="radio"
                            v-model.number="selectColorId"
                            name="color-item"
                            :value="itemColor.color.id"
                          />
                          <span
                            class="colors__value"
                            :style="{ background: itemColor.color.code }"
                          >
                          </span>
                        </label>
                      </li>
                    </ul>
                  </fieldset>

                  <fieldset class="form__block">
                    <legend class="form__legend">Размер</legend>

                    <SelectSize
                      :sizes="product.sizes"
                      :select-size.sync="selectSizeId"
                      :smaller="false"
                    />
                  </fieldset>
                </div>

                <button class="item__button button button--primery" type="submit">
                  В корзину
                </button>

                <h3 v-if="this.productAddedCart">Товар добавлен в корзину</h3>
              </form>
            </div>
          </div>

          <div class="item__desc">
            <ul class="tabs">
              <li class="tabs__item">
                <a
                  class="tabs__link"
                  :class="{ 'tabs__link--current': showDescription }"
                  @click.prevent="changeDescription"
                >
                  Информация о товаре
                </a>
              </li>
              <li class="tabs__item">
                <a
                  class="tabs__link"
                  :class="{ 'tabs__link--current': !showDescription }"
                  @click.prevent="changeDescription"
                >
                  Доставка и возврат
                </a>
              </li>
            </ul>

            <div class="item__content" v-show="showDescription">
              <h3>Состав:</h3>

              <p v-html="materialStructure"></p>

              <h3>Уход:</h3>

              <p>
                Машинная стирка при макс. 30ºC короткий отжим<br />
                Гладить при макс. 110ºC<br />
                Не использовать машинную сушку<br />
                Отбеливать запрещено<br />
                Не подвергать химчистке<br />
              </p>
            </div>

            <div class="item__content" v-show="!showDescription">
              <h3>Доставка:</h3>

              <p>
                1. Курьерская доставка по Москве и Московской области –
                {{ deliveriesInfo }} <br />
                2. Самовывоз из магазина. Список и адреса магазинов Вы можете посмотреть
                <a>здесь</a><br />
              </p>

              <h3>Возврат:</h3>

              <p>
                Любой возврат должен быть осуществлен в течение Возвраты в магазине
                БЕСПЛАТНО.<br />
                Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в
                стране, в которой Вы осуществили покупку. БЕСПЛАТНЫЙ возврат в Пункт
                выдачи заказов.<br />
                Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов,
                позвоните по телефону 8 800 600 90 09<br />
              </p>
            </div>
          </div>
        </section>
      </div>

      <WarningMessage
        v-if="loadError"
        :errorMessage="errorMessage"
        @tryAgain="loadProduct"
      />

      <ShowPreloader v-show="showPreloader" />
    </main>
  </div>
</template>
// 99999999999999999999 9999999999999999999999----------------- 9999999999999999999999

<script>
import numberFilterMixin from "@/mixins/numberFilterMixin.js";
import showServiceMixin from "@/mixins/showServiceMixin";
import { NO_FOTO } from "@/config.js";
import QuantityMinus from "@/components/QuantityMinus.vue";
import QuantityPlus from "@/components/QuantityPlus.vue";
import { mapActions, mapMutations, mapGetters } from "vuex";
import deliveryMixin from "@/mixins/deliveryMixin.js";
import SelectSize from "@/components/SelectSize.vue";

export default {
  components: { QuantityMinus, QuantityPlus, SelectSize },

  data() {
    return {
      product: null,
      urlImages: [],
      urlImage: "",
      showDescription: true,

      selectColorId: 0,
      selectSizeId: 0,
      quantity: 1,
    };
  },

  mixins: [numberFilterMixin, showServiceMixin, deliveryMixin],

  watch: {
    quantity() {
      if (this.quantity === "") {
        setTimeout(() => {
          this.quantity = this.quantity === "" ? 1 : this.quantity;
        }, 1000);
      }
      this.updateProductAddedCart(false);
      return this.quantity > 0 ? this.quantity : 1;
    },

    selectColorId() {
      this.updateProductAddedCart(false);
      this.urlImages = [];
      const i = this.product.colors.findIndex(
        (item) => item.color.id === this.selectColorId
      );
      if (this.product.colors[i].gallery) {
        this.product.colors[i].gallery.map((itemGal) => {
          this.urlImages.push(itemGal.file.url);
        });
      } else {
        this.urlImages.push(NO_FOTO);
      }
    },

    selectSizeId() {
      this.updateProductAddedCart(false);
    },

    urlImages() {
      if (this.urlImages[0]) {
        this.changeUrlImage(0);
      }
    },

    "$route.params.id": {
      handler() {
        this.loadProduct();
        this.updateProductAddedCart(false);
      },
    },
  },

  computed: {
    ...mapGetters({ productAddedCart: "productAddedCartGetter" }),

    materialStructure() {
      let result = "";
      if (this.product.materials) {
        this.product.materials.map((item) => {
          result = result + item.title + "<br>";
        });
      } else {
        result = "Нет данных";
      }
      return result;
    },
  },

  methods: {
    ...mapActions(["addToCart", "loadCart", "loadProductById"]),
    ...mapMutations([
      "updateProductAddedCart",
      "updateFilterCategoryId",
      "updateIsFiltered",
    ]),

    updateCategoryId() {
      this.updateFilterCategoryId(this.product.category.id);
      this.updateIsFiltered(true);
      this.$router.push({ name: "main" });
    },

    changeQuantity(value) {
      this.quantity += value;
      if (this.quantity < 1) {
        this.quantity = 1;
      }
    },

    submit() {
      if (this.product) {
        this.startLoad();
        this.addToCart({
          productId: this.product.id,
          colorId: this.selectColorId,
          sizeId: this.selectSizeId,
          quantity: this.quantity,
        })
          .then(() => {
            this.endLoadOk();
          })
          .catch((error) => {
            this.endLoadError();
            this.fillErrorMessage(
              error,
              "Ошибка добавления товара в корзину. Обновите страницу или попробуйте позже...",
              true
            );
          })
          .then(() => {
            this.startLoad();
            this.loadCart()
              .then(() => {
                this.endLoadOk();
              })
              .catch((error) => {
                this.endLoadError();
                this.fillErrorMessage(
                  error,
                  "Ошибка загрузки корзины с товарами. Обновите страницу или попробуйте позже...",
                  true
                );
              });
          });
      }
    },

    changeDescription() {
      if (this.showDescription) {
        this.showDescription = false;
      } else {
        this.showDescription = true;
      }
    },

    changeUrlImage(i) {
      this.urlImage = this.urlImages[i];
    },

    loadProduct() {
      this.startLoad();
      this.loadProductById(this.$route.params.id)
        .then((respons) => (this.product = respons.data))
        .catch((error) => {
          this.product = null;
          this.endLoadError();
          this.fillErrorMessage(
            error,
            "Ошибка загрузки данных о товаре. Попробуйте позже...",
            true
          );
        })
        .then(() => {
          this.endLoadOk();
          if (this.product) {
            this.selectColorId = this.product.colors[0].color.id;
            this.selectSizeId = this.product.sizes[0].id;
          }
        });
    },
  },

  created() {
    this.loadProduct();
    this.loadDelivery();
  },

  destroyed() {
    this.updateProductAddedCart(false);
  },
};
</script>

<style>
.item__content a {
  border-bottom: none;
  font-size: 16px;
}

.item__content a:active {
  border-bottom: 2px solid #e02d71;
}

.item__content a:hover {
  border-bottom: 2px solid #e02d71;
}
</style>
