<template>
  <div>
    <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a
          class="pagination__link pagination__link--arrow"
          :class="{ 'pagination__link--disabled': pagination.page === 1 }"
          aria-label="Предыдущая страница"
          @click.prevent="paginate(pagination.page - 1)"
        >
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>

      <li class="pagination__item" v-for="pageNum in pagination.totalPage" :key="pageNum">
        <a
          class="pagination__link"
          :class="{ 'pagination__link--current': pageNum === pagination.page }"
          @click.prevent="paginate(pageNum)"
        >
          {{ pageNum }}
        </a>
      </li>

      <li class="pagination__item">
        <a
          class="pagination__link pagination__link--arrow"
          href="#"
          :class="{
            'pagination__link--disabled': pagination.page === pagination.totalPage,
          }"
          aria-label=" Следующая страница"
          @click.prevent="paginate(pagination.page + 1)"
        >
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>

    <span style="display: flex; justify-content: flex-end">
      Количество на странице

      <select
        class="form__select--smaller"
        type="text"
        name="cnt"
        style="margin-left: 10px"
        v-model="selectLimit"
      >
        <option v-for="item in listLimit" :key="item" :value="item">
          {{ item }}
        </option>
      </select>
    </span>
  </div>
</template>

//999999999999999 99999999999999999999999999999999999 -----------------------9999999

<script>
import { mapMutations } from "vuex";

export default {
  props: ["pagination", "listLimit"],

  data() {
    return {
      selectLimit: 0,
      selectPage: 0,
    };
  },

  methods: {
    ...mapMutations(["updateCurrentPage", "updateCurrentLimit"]),

    paginate(page) {
      this.updateCurrentPage(page);
      this.selectPage = page;
      this.changeLimit();
    },

    changeLimit() {
      this.updateCurrentLimit(this.selectLimit);
      this.$emit("change-page", this.selectPage, this.selectLimit);
    },
  },

  watch: {
    selectLimit() {
      this.changeLimit();
    },
  },

  created() {
    this.selectLimit = this.pagination.limit;
    this.selectPage = this.pagination.page;
  },
};
</script>

<style>
.pagination__link--disabled {
  pointer-events: none;
}
</style>
