import Vue from "vue";
import VueRouter from "vue-router";
import MainPage from "@/components/Pages/MainPage.vue";
import CartPage from "@/components/Pages/CartPage.vue";
import ProductCardPage from "@/components/Pages/ProductCardPage.vue";
import OrderPageVue from "@/components/Pages/OrderPage.vue";
import OrderConfirmedPage from "@/components/Pages/OrderConfirmedPage.vue";
Vue.use(VueRouter);

const routes = [
  { name: 'main', component: MainPage, path: '/' },
  { name: 'cart', component: CartPage, path: '/cart' },
  { name: 'card', component: ProductCardPage, path: '/card/:id' },
  { name: 'order', component: OrderPageVue, path: '/order' },
  { name: 'confirm', component: OrderConfirmedPage, path: '/confirm/:id' }
];
const router = new VueRouter({ routes });

export default router;
